@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,400;0,700;1,400;1,700&display=swap);
:root {
  --cc-charcoal: #3f3f3f;
  --cc-charcoal-hint: #828282;
  --cc-charcoal-light1: #979797;
  --cc-charcoal-light2: #dfdfdf;
  --cc-teal: #0592af;
  --cc-teal-dark2: #016082;
  --cc-teal-dark1: #0481a0;
  --cc-teal-light1: #2ba5c1;
  --cc-teal-light2: #6fc6da;
  --cc-teal-light3: #93d5e4;
  --cc-teal-light4: #b7e2ec;
  --cc-teal-light5: #cdebf2;
  --cc-teal-light6: #e2f4f8;
  --cc-teal-light7: #f5fbfc;
  --cc-orange: #ff8415;
  --cc-orange-light1: #ffa350;
  --cc-orange-light2: #ffc18a;
  --cc-orange-light3: #ffcea1;
  --cc-orange-light4: #ffe6d0;
  --cc-orange-light4B: #ffeddc;
  --cc-orange-light5: #fff2e7;
  --cc-orange-light6: #fff9f3;
  --feedback-green: #4ea15a;
  --feedback-green-light1: #7bb984;
  --feedback-green-light1B: #8cc294;
  --feedback-green-light2: #a6d0ac;
  --feedback-green-light3: #b8d9bd;
  --feedback-green-light3B: #c9e2cd;
  --feedback-green-light4: #dcecde;
  --feedback-green-light4B: #e5f1e7;
  --feedback-green-light5: #edf5ee;
  --feedback-green-light6: #f6faf7;
}


body {
  margin: 0;
  font-family: 'Montserrat', 'Lato', 'Helvetica',sans-serif;
  font-size: 16px;
  color: var(--cc-charcoal);
  overflow-x: hidden;
}
a {
  text-decoration: none;
  color: var(--cc-charcoal);
  cursor: pointer;
}


.header {
  top: 0;
  width: 100%;
  /* height: 50px; */
}

.headerTitle {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  background-color: var(--cc-teal-light6);
  padding: 10px;
  border-bottom: solid 1px var(--cc-charcoal-light2);
  position: -webkit-sticky;
  position: sticky;
  z-index: 1;
}

.codap-logo {
  height: auto;
  width: 120px;
}

.headerCenter {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex: 1 1;
          flex: 1 1;
}

.title {
  font-size: 24px;
}

.navigationTabs {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  position: relative;
  top: 15px;
  height: 40px;
  border-bottom: solid 1px var(--cc-charcoal-light1);
  list-style-type: none;
  padding: 0;
}

.tab {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  box-sizing: border-box;
  height: 40px;
  border-radius: 10px 10px 0 0;
  padding: 0 20px;
  margin: 0 5px;
  border-top: solid 1px var(--cc-charcoal-light1);
  border-left: solid 1px var(--cc-charcoal-light1);
  border-right: solid 1px var(--cc-charcoal-light1);
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  cursor: pointer;
}
.tab:active {
  color: white;
}

.tab.Partners {
  background-color: var(--cc-teal-light5);
}

.tab.Utilities {
  background-color: var(--cc-orange-light4);
}

.tab.Data.Games {
  background-color: var(--cc-charcoal-light2);
}

.tab.Data.Worlds {
background-color: var(--feedback-green-light4);
}

.tab.Partners:hover {
  background-color: var(--cc-teal-light3);
}

.tab.Utilities:hover {
  background-color: var(--cc-orange-light2);
}

.tab.Data.Games:hover {
  background-color: var(--cc-charcoal-light1);
}

.tab.Data.Worlds:hover {
background-color: var(--feedback-green-light2);
}

.tab.Partners.selected {
    background-color: var(--cc-teal-light3);
}

.tab.Utilities.selected {
    background-color: var(--cc-orange-light2);
}

.tab.Data.Games.selected {
    background-color: var(--cc-charcoal-light1);
}

.tab.Data.Worlds.selected {
  background-color: var(--feedback-green-light2);
}

ul {
  margin: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-padding-start: 0;
          padding-inline-start: 0;
}
.card {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  border: 1px solid grey;
  border-radius: 5px;
  padding: 8px;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  -webkit-transform: translateZ(0);
          transform: translateZ(0);
  transition: -webkit-transform 0.25s ease-out;
  transition: transform 0.25s ease-out;
  transition: transform 0.25s ease-out, -webkit-transform 0.25s ease-out;
  color: var(--cc-charcoal);
  min-height: 200px;
  background-color: white;
}

