.pluginTitle {
  font-weight: bold;
  font-size: 18px;
}
.pluginTitle:hover {
  color: var(--cc-teal-dark1);
}
.pluginTitle:active {
  color: var(--cc-teal-dark2);
}

.pluginDescription {
  margin: 10px;
}
.pluginDescription > a {
  text-decoration: none;
  color: var(--cc-teal-dark1);
}

.embeddableLink {
  position: absolute;
  bottom: 10px;
  font-size: 12px;
}
.embeddableLink:hover {
  color: var(--cc-teal-dark1);
}
.embeddableLink:active {
  color: var(--cc-teal-dark2);
}

.cardlistWrapper {
  width: 100%;
}
.card-list {
  margin: 15px 20px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  grid-gap: 20px;
  padding: 15px 0;
}

.cardlistWrapper.Partners {
  background-color: var(--cc-teal-light5);
}
.cardlistWrapper.Utilities {
  background-color: var(--cc-orange-light4);
}
.cardlistWrapper.Data.Games {
  background-color: var(--cc-charcoal-light2);
}
.cardlistWrapper.Data.Worlds {
  background-color: var(--feedback-green-light4);
}
.footer {
  background-color: var(--cc-teal-light3);
  bottom: 0;
  height: 120px;
  width: 100%;
  color: var(--cc-teal-light1);
  font-size: 12px;
  position: relative;
}

#cc {
  position: absolute;
  right: 25px;
  bottom: 25px;
}

#cc img {
  width: 120px;
  height: auto;
}
